import { Component } from 'react'
import fetch from 'isomorphic-unfetch'
import { object, string, number } from 'yup'
import Widget from '../../widget'
import Version from '../../version'
import { basicAuthHeader } from '../../../lib/auth'
import Table, { Th, Td } from '../../table'

const schema = object().shape({
  owner: string().required(),
  repository: string().required(),
  interval: number(),
  title: string(),
  authKey: string(),
  bearerToken: string(),
  environment: string()
})

export default class VersionNumber extends Component {
  static defaultProps = {
    interval: 1000 * 60 * 5,
    title: 'Version Number',
    environment: 'N/A '
  }

  state = {
    count: 0,
    error: false,
    loading: true
  }

  componentDidMount () {
    schema.validate(this.props)
      .then(() => this.fetchInformation())
      .catch((err) => {
        console.error(`${err.name} @ ${this.constructor.name}`, err.errors)
        this.setState({ error: true, loading: false })
      })
  }

  componentWillUnmount () {
    clearTimeout(this.timeout)
  }

  async fetchInformation () {
    const { authKey, owner, repository, bearerToken } = this.props
    const opts = authKey ? { headers: basicAuthHeader(authKey) } : {}

    try {
      
      const res = await fetch(`https://api.github.com/repos/${owner}/${repository}/releases/latest`, {
        headers: {
          'Authorization': 'Bearer ' + bearerToken,
          'Content-Type': 'application/json'
        } 
      });
      const json = await res.json()

      this.setState({ version: json.tag_name, error: false, loading: false })
    } catch (error) {
      this.setState({ error: true, loading: false })
    } finally {
      this.timeout = setTimeout(() => this.fetchInformation(), this.props.interval)
    }
  }

  render () {
    const { version, error, loading } = this.state
    const { title } = this.props
    return (
      <Widget title={title} loading={loading} error={error}>
          <Table>
          <tbody>
            <tr>
              <Th>{this.props.environment}</Th>
              <Td>
                <Version value={version} />
              </Td>
            </tr>
          </tbody>
        </Table>
      </Widget>
    )
  }
}
